// store.js

import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        params: null
    },
    mutations: {
        setParams(state, params) {
            state.params = params;
        }
    },
    actions: {
        updateParams({ commit }, params) {
            commit('setParams', params);
        }
    }
});
