import { apiGet, apiPost } from "./login_api";

export function getCode() {
    return new Promise((resolve, reject) => {
        apiGet("index.php/index/index/getcode").then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        })
    })
}

export function register(obj) {
    return new Promise((resolve, reject) => {
        apiPost("index.php/index/index/register", obj).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        })
    })
}

