import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/LoginView.vue'
import Register from '../views/RegisterView.vue'
import Home from '../views/Home_html/HomeView.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
  },
  {
    path: '/Register',
    name: 'Register',
    component: Register
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    redirect: '/Subpage',
    children: [
      {
        path: '/Subpage',
        name: 'Subpage',
        redirect: '/Enterprise',
        component: () => import('../views/Home_html/SubpageView.vue'),
        children: [
          {
            path: '/Enterprise',
            name: 'Enterprise',
            component: () => import('../views/Home_html/Enterprise_data .vue')
          },
          {
            path: '/Provincial',
            name: 'Provincial',
            component: () => import('../views/Home_html/Provincial_data.vue')
          },
          {
            path: '/Server',
            name: 'Server',
            component: () => import('../views/Home_html/Server_data.vue')
          }
        ]
      },
      {
        path: '/Business',
        name: 'Business',
        component: () => import('../views/business_html/BusinessView.vue'),
        redirect: '/Super',
        children: [
          {
            path: '/Super',
            name: 'Super',
            component: () => import('../views/business_html/SuperView.vue')
          },
          {
            path: '/Son',
            name: 'Son',
            component: () => import('../views/business_html/SonView.vue')
          }
        ]
      },
      {
        path: '/Permission',
        name: 'Permission',
        component: () => import('../views/Permission_html/PermissionView.vue')
      },
      {
        path: '/Call',
        name: 'Call',
        component: () => import('../views/system_html/CallView.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
