<template>
  <div class="content">
    <div style="position: relative; top: 10%">
      <div class="main_register">
        <div style="width: 40%; height: 60%">
          <img
            src="https://zytest.emicloud.com/static/img/pic.5be40c0.png"
            alt=""
          />
        </div>
        <div style="width: 40%; height: 80%">
          <div class="title">
            <img
              src="../assets/title.jpg"
              alt=""
              style="width: 70%; height: 55%; margin-left: -10px"
            />
          </div>
          <div class="login">
            <el-form
              :label-position="labelPosition"
              label-width="80px"
              :model="formLabelAlign"
              :rules="rules"
              ref="form"
            >
              <div class="from_img">
                <el-form-item
                  prop="name"
                  style="margin-left: -80px; margin-bottom: 0px"
                >
                  <el-input
                    v-model="formLabelAlign.name"
                    placeholder="用户名"
                    prefix-icon="el-icon-user"
                    style="width: 100px"
                  ></el-input>
                </el-form-item>
              </div>
              <div class="from_img">
                <el-form-item
                  prop="pwd"
                  style="margin-left: -80px; margin-bottom: 0px"
                >
                  <el-input
                    v-model="formLabelAlign.pwd"
                    placeholder="密码"
                    prefix-icon="el-icon-lock"
                    style="width: 400px"
                  ></el-input>
                </el-form-item>
              </div>
              <div class="from_img">
                <el-form-item
                  prop="emails"
                  style="margin-left: -80px; margin-bottom: 0px"
                >
                  <el-input
                    v-model="formLabelAlign.emails"
                    placeholder="邮箱"
                    prefix-icon="el-icon-chat-line-square"
                    style="width: 400px"
                  ></el-input>
                </el-form-item>
              </div>
              <div>
                <button type="primary" class="register_button" @click="goBack">
                  返回
                </button>
                <el-button
                  type="primary"
                  round
                  class="from_button"
                  @click="register"
                >
                  注册
                </el-button>
              </div>
            </el-form>
          </div>
        </div>
        <div class="top_img">
          <img src="../assets/mihua.png" alt="" />
        </div>
        <div class="right_img">
          <img src="../assets/weixin.jpg" alt="" />
