    </div>
  </div>
</template>

<script>
import { register } from "../api/login_http";

export default {
  data() {
    return {
      labelPosition: "right",
      formLabelAlign: {
        name: "",
        pwd: "",
        emails: "",
      },
      rules: {
        name: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        pwd: [{ required: true, message: "请输入密码", trigger: "blur" }],
        emails: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            type: "email",
            message: "请输入有效的邮箱地址",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },

  methods: {
    register() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          register(this.formLabelAlign).then((res) => {
            if (res.msg == "用户名重复") {
              this.$alert(res.msg, "提示", {
                confirmButtonText: "确定",
              });
            } else {
              this.$alert("注册成功", "提示", {
                confirmButtonText: "确定",
                callback: () => {
                  this.$router.push({ path: "/" });
                },
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    goBack() {
      this.$router.push({ path: "/" });
    },
  },
};
</script>

