<template>
  <div class="content">
    <div style="position: relative; top: 10%">
      <div class="main">
        <div style="width: 40%; height: 60%">
          <img
            src="https://zytest.emicloud.com/static/img/pic.5be40c0.png"
            alt=""
          />
        </div>
        <div style="width: 40%; height: 80%">
          <div class="title">
            <img
              src="../assets/title.jpg"
              alt=""
              style="width: 70%; height: 55%; margin-left: -10px"
            />
          </div>
          <div class="login">
            <el-form
              :label-position="labelPosition"
              label-width="80px"
              :model="formLabelAlign"
            >
              <div class="from_img">
                <el-input
                  v-model="formLabelAlign.name"
                  placeholder="用户名"
                  prefix-icon="el-icon-user"
                ></el-input>
              </div>
              <div class="from_img">
                <el-input
                  v-model="formLabelAlign.pwd"
                  placeholder="密码"
                  prefix-icon="el-icon-lock"
                  show-password
                  auto-complete="off"
                ></el-input>
              </div>
              <div class="from_img">
                <el-input
                  v-model="formLabelAlign.vercode"
                  placeholder="验证码"
                  prefix-icon="el-icon-chat-line-square"
                ></el-input>
                <div @click="getCode()" style="font-size: 20px">
                  {{ code }}
                </div>
              </div>
              <div>
                <button
                  type="primary"
                  class="register_button"
                  @click="register()"
                >
                  注册
                </button>
                <el-button
                  type="primary"
                  round
                  class="from_button"
                  @click="login()"
                  >登录</el-button
                >
              </div>
            </el-form>
          </div>
        </div>
        <div class="top_img">
          <img src="../assets/mihua.png" alt="" />
        </div>
        <div class="right_img">
          <img src="../assets/weixin.jpg" alt="" />
        </div>
      </div>
      <div
        style="
          width: 1000px;
