        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { getCode, login } from "../api/login_http";

export default {
  data() {
    return {
      code: null,
      labelPosition: "right",
      formLabelAlign: {
        name: "",
        pwd: "",
        vercode: "",
      },
    };
  },
  mounted() {
    this.getCode();
  },
  methods: {
    getCode() {
      getCode().then((res) => {
        console.log(res);
        this.code = res.msg;
      });
    },
    register() {
      this.$router.push({ path: "/register" });
    },
    login() {
      if (
        this.formLabelAlign.name == "" ||
        this.formLabelAlign.pwd == "" ||
        this.formLabelAlign.vercode == ""
      ) {
        this.$alert("请输入完整信息", "提示", {
          confirmButtonText: "确定",
        });
      } else {
        login(this.formLabelAlign).then((res) => {
          console.log(res);
          if (res.msg == "验证码不正确") {
            this.$alert(res.msg, "提示", {
              confirmButtonText: "确定",
            });
          } else if (res.msg == "登陆失败") {
            this.$alert("用户名或密码错误", "提示", {
              confirmButtonText: "确定",
            });
          } else {
            this.$router.push({ path: "/Home" });
          }
        });
      }
    },
  },
};
</script>

